<template>
  <form class="card auth-card" @submit.prevent="">
    <div class="card-content">
      <img class="logo-image" :style="''" src="@/assets/newImages/logo.png">
      <div class="input-field">
        <label for="email"
          >{{ $localizeFilter('email') }}<span class="required">*</span></label
        >
        <input
          id="email"
          type="text"
          v-model.trim="email"
          :class="{ invalid: v$.email.$error }"
        />
        <small
          class="error"
          v-for="(error, index) of v$.email.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>
      <div class="input-field">
        <label for="password">{{ $localizeFilter('password') }}<span class="required">*</span></label>
        <input
        id="password"
        type="password"
        v-model.trim="password"
        :class="{ invalid: v$.password.$error }"
        />
         <small
          class="error"
          v-for="(error, index) of v$.password.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>

      <div class="input-field">
        <label for="lastName">{{ $localizeFilter('last_name') }}<span class="required">*</span></label>
        <input
        id="lastName"
        type="text"
        v-model.trim="lastName"
        :class="{ invalid: v$.lastName.$error }"
        />
         <small
          class="error"
          v-for="(error, index) of v$.lastName.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>
      
      <div class="input-field">
        <label for="firstName">{{ $localizeFilter('first_name') }}<span class="required">*</span></label>
        <input
        id="firstName"
        type="text"
        v-model.trim="firstName"
        :class="{ invalid: v$.firstName.$error }"
        />
         <small
          class="error"
          v-for="(error, index) of v$.firstName.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>

      <!-- <div class="input-field">
        <label for="middleName">{{ $localizeFilter('middleName') }}<span class="required">*</span></label>
        <input
        id="middleName"
        type="text"
        v-model.trim="middleName"
        :class="{ invalid: v$.middleName.$error }"
        />
         <small
          class="error"
          v-for="(error, index) of v$.middleName.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div> -->

      <div class="input-field disabled">
        <label class="active" for="sponsorEmail">{{ $localizeFilter('sponsor_email') }}<span class="required">*</span></label>
        <input
        id="sponsorEmail"
        type="text"
        v-model.trim="sponsorEmail"
        :class="{ invalid: v$.sponsorEmail.$error }"
        />
         <small
          class="error"
          v-for="(error, index) of v$.sponsorEmail.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>
    </div>

    <div class="card-action">
      <div>
        <button class="button-2" id="submitButton" @click.once="submitHandler" :key="buttonKey">
          <p class="button-title">{{$localizeFilter('register')}}</p>
          <i class="material-icons right">send</i>
        </button>
      </div>

      <p class="additional-action">
        {{ $localizeFilter('already_have_an_account') }}
        <router-link :to="(buyCoinTicker ? `/login?buyCoinTicker=${buyCoinTicker}` : '/login')">{{ $localizeFilter('sign_in') }}</router-link>
      </p>
    </div>
  </form>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, email, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
  props: {
    token: {
      type: String,
      default: null
    },
    buyCoinTicker: {
      type: String,
      default: null
    }
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      email: "",
      password: "",
      firstName: "",
      lastName: "",
      // middleName: "",
      sponsorEmail: "",
      buttonKey: 1
    };
  },
  validations() {
    return {
      email: {
       required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
       email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email),
      },
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      firstName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      lastName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      // middleName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      sponsorEmail: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email),
      }
    };
  },
  methods: {
    openBot() {
      // window.open('')
    },
    async submitHandler() {
      this.v$.$touch();
      if (this.v$.$error) {
        setTimeout(()=>{
          this.buttonKey+=1
        }, 1000)
        return;
      }
      const el = document.getElementById('submitButton'); 
      el.classList.add("button--loading");
      const formData = {
        email: this.email,
        firstName: this.firstName,
        lastName: this.lastName,
        sponsorEmail:this.sponsorEmail,
        password: this.password, 
        buyCoinTicker: this.buyCoinTicker
      }
      try {
        await this.$store.dispatch('register', formData)
        await this.$store.dispatch('login', formData)
        const {scope, email} = await this.$store.dispatch('getProfile')
        setTimeout(()=>{
          el.classList.remove("button--loading")
          if (scope=='not_activated_client') {
            this.$router.push('/confirmEmail?email='+email)
          } else {
            this.$router.push('/cabinet')
          }
          this.$message(localizeFilter('registration_successfull'))
        },1000)
      } catch (e) {
        setTimeout(()=>{
            this.$error(e)
            el.classList.remove("button--loading")
            this.buttonKey+=1
        }, 1000)
      }
    },
  },
  computed: {
  },
  async mounted() {
    if(this.token){
      localStorage.referralToken = this.token;
    }
    const token = this.token || localStorage.referralToken;
    try {
      if(token){
        const data = await this.$store.dispatch('getEmailByReferralToken',{token})
        this.sponsorEmail = data.email
      } else {
        this.sponsorEmail = 'root@capitalwhale.io'
      }
        document.getElementById('email').focus()
      } catch (e) {
          this.$error(e)
      }
  }
});
</script>

